.groupIcon,
.healthcare1 {
  position: relative;
  width: 64px;
  height: 64.81px;
}
.healthcare1 {
  font-size: var(--font-size-xl);
  font-family: var(--font-rubik);
  color: var(--color-black);
  text-align: left;
  display: inline-block;
  width: 103px;
  height: 24.3px;
  flex-shrink: 0;
}
.healthcare {
  cursor: pointer;
  border: 1px solid var(--color-khaki);
  padding: 37.46825408935547px 0;
  background-color: transparent;
  flex: 1;
  border-radius: var(--br-6xl);
  background: linear-gradient(180deg, #fffae0, rgba(255, 255, 255, 0));
  box-shadow: 0 4px 74px rgba(199, 231, 225, 0.27);
  box-sizing: border-box;
  height: 224.81px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
@media screen and (max-width: 960px) {
  .healthcare {
    flex: unset;
    align-self: stretch;
  }
}
