.daycare1,
.groupIcon {
  position: relative;
  width: 106.89px;
  height: 73.55px;
}
.daycare1 {
  display: inline-block;
  width: 76px;
  height: 24.3px;
  flex-shrink: 0;
}
.daycare {
  flex: 1;
  border-radius: var(--br-6xl);
  background: linear-gradient(180deg, #f9fffe, rgba(255, 255, 255, 0));
  box-shadow: 0 4px 74px rgba(199, 231, 225, 0.27);
  border: 1px solid var(--color-paleturquoise);
  box-sizing: border-box;
  height: 224.81px;
  display: flex;
  flex-direction: column;
  padding: 37.46825408935547px 0;
  align-items: center;
  justify-content: space-between;
}
.groupIcon1,
.training1 {
  position: relative;
  width: 65px;
  height: 71.05px;
}
.training1 {
  display: inline-block;
  width: 75px;
  height: 24.3px;
  flex-shrink: 0;
}
.training {
  flex: 1;
  border-radius: var(--br-6xl);
  background: linear-gradient(180deg, #fffae0, rgba(255, 255, 255, 0));
  box-shadow: 0 4px 74px rgba(199, 231, 225, 0.27);
  border: 1px solid var(--color-khaki);
  box-sizing: border-box;
  height: 224.81px;
  display: flex;
  flex-direction: column;
  padding: 37.46825408935547px 0;
  align-items: center;
  justify-content: space-between;
}
.groupIcon2,
.hyginicCare1 {
  position: relative;
  width: 81.74px;
  height: 66.84px;
}
.hyginicCare1 {
  display: inline-block;
  width: 116px;
  height: 24.3px;
  flex-shrink: 0;
}
.contentsServices,
.mainPage {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.contentsServices {
  align-self: stretch;
  flex-direction: row;
  padding: 0 var(--padding-81xl);
  gap: 31px;
}
.mainPage {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  height: 1164px;
  overflow: hidden;
  flex-direction: column;
  gap: 115px;
  text-align: left;
  font-size: var(--font-size-xl);
  color: var(--color-black);
  font-family: var(--font-rubik);
}
@media screen and (max-width: 960px) {
  .daycare,
  .training {
    flex: unset;
    align-self: stretch;
  }
  .contentsServices {
    flex-direction: column;
    gap: 20px;
  }
}
@media screen and (max-width: 420px) {
  .contentsServices {
    gap: var(--gap-3xs);
  }
}
