@import url("https://fonts.googleapis.com/css2?family=Sansation:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Pangolin:wght@400&display=swap");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-rubik: Rubik;
  --font-noto-sans-kr: "Noto Sans KR";
  --font-sansation: Sansation;
  --font-pangolin: Pangolin;

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-sm: 14px;
  --font-size-12xl: 31px;

  /* Colors */
  --color-white: #fff;
  --color-paleturquoise: #98f2e1;
  --color-black: #000;
  --color-khaki: #ffee94;
  --greyscale-01333: #333;
  --color-gold: #ffe55e;
  --color-orange: #ffaa00;

  /* Gaps */
  --gap-3xs: 10px;
  --gap-base: 16px;
  --gap-5xs: 8px;
  --gap-13xl: 32px;
  --gap-6xs: 7px;

  /* Paddings */
  --padding-81xl: 100px;
  --padding-18xl-5: 37.5px;
  --padding-12xs-1: 0.1px;
  --padding-13xl: 32px;
  --padding-10xs: 3px;
  --padding-3xs: 10px;
  --padding-8xs: 5px;

  /* border radiuses */
  --br-6xl: 25px;
}
