@keyframes shadow-inset-center {
  0% {
    box-shadow: inset 0 0 0 0 transparent;
  }
  to {
    box-shadow: inset 0 0 14px 0 rgb(0 0 0/50%);
  }
}
.grooming2,
.vectorIcon {
  position: relative;
  width: 86.15px;
  height: 93.77px;
}
.grooming2 {
  font-size: var(--font-size-xl);
  font-family: var(--font-rubik);
  color: var(--color-black);
  text-align: left;
  display: inline-block;
  width: 91px;
  height: 24.3px;
  flex-shrink: 0;
}
.grooming,
.grooming1 {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.grooming1 {
  width: 91.15px;
  height: 134.68px;
  padding: 0 0.14598846435546875px 0 0;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.grooming {
  cursor: pointer;
  border: 1px solid var(--color-paleturquoise);
  padding: 37.46828842163086px 0;
  background-color: transparent;
  flex: 1;
  border-radius: var(--br-6xl);
  background: linear-gradient(180deg, #f9fffe, rgba(255, 255, 255, 0));
  box-shadow: 0 4px 74px rgba(199, 231, 225, 0.27);
  height: 224.81px;
  justify-content: space-between;
}
.grooming:hover {
  animation: 3s ease 0s infinite normal none shadow-inset-center;
  opacity: 1;
}
@media screen and (max-width: 960px) {
  .grooming {
    flex: unset;
    align-self: stretch;
  }
}
