.navigationMenu {
  text-decoration: none;
  position: relative;
  width: 21px;
  height: 21px;
  display: none;
}
.logoIcon {
  position: relative;
  width: 43.71px;
  height: 34.81px;
}
.hub {
  color: var(--color-gold);
}
.puphub {
  position: relative;
}
.navigationLogo1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-6xs);
}
.navigationLogo {
  width: 185.71px;
  height: 49px;
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  padding: 0 var(--padding-8xs);
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: center;
}
.div,
.div1,
.research {
  position: relative;
  font-weight: 500;
}
.div {
  color: var(--color-black);
  display: inline-block;
  width: 122px;
  flex-shrink: 0;
}
.div1,
.research {
  line-height: 20px;
}
.div1 {
  display: inline-block;
  width: 103px;
  flex-shrink: 0;
}
.research {
  flex: 1;
}
.navigationLinks {
  text-decoration: none;
  width: 608.29px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-13xl);
  text-align: center;
  font-size: var(--font-size-sm);
  color: var(--greyscale-01333);
  font-family: var(--font-noto-sans-kr);
}
.navigationButton {
  padding: var(--padding-10xs) var(--padding-3xs);
  justify-content: flex-end;
  gap: var(--gap-5xs);
}
.header,
.headerNavigation,
.navigationButton {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.headerNavigation {
  flex: 1;
  justify-content: space-between;
}
.header {
  align-self: stretch;
  background-color: var(--color-white);
  padding: var(--padding-13xl) var(--padding-81xl);
  justify-content: center;
  text-align: left;
  font-size: var(--font-size-12xl);
  color: var(--color-orange);
  font-family: var(--font-sansation);
}
@media screen and (max-width: 1200px) {
  .navigationMenu {
    display: none;
  }
  .navigationLinks {
    display: flex;
  }
}
@media screen and (max-width: 960px) {
  .navigationMenu {
    display: flex;
  }
  .navigationLinks {
    display: none;
  }
}
@media screen and (max-width: 420px) {
  .navigationMenu {
    display: flex;
  }
  .navigationButton {
    height: 40px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
